/** Locales shown in the language picker. */
export const AVAILABLE_LOCALES: Array<{ value: Locale; label: string; nativeLabel: string }> = [
  { value: "en", label: "English", nativeLabel: "English" },
  { value: "zh-CN", label: "Chinese (Simplified)", nativeLabel: "" },
  { value: "zh-TW", label: "Chinese (Traditional)", nativeLabel: "" },
  { value: "ja", label: "Japanese", nativeLabel: "" },
  { value: "ko", label: "Korean", nativeLabel: "" },
  { value: "es", label: "Spanish", nativeLabel: "Espaol" },
  { value: "fr", label: "French", nativeLabel: "Franais" },
  { value: "de", label: "German", nativeLabel: "Deutsch" },
  { value: "pt-BR", label: "Portuguese (Brazil)", nativeLabel: "Portugus (Brasil)" },
  { value: "ru", label: "Russian", nativeLabel: "" },
];

export function getMessages(locale: Locale): I18nMessages {
  return MESSAGES[locale] ?? EN;
}

/** Detect the best initial locale from the browser. */
export function detectLocale(): Locale {
  const nav = navigator.language ?? "en";
  const lower = nav.toLowerCase();
  if (lower.startsWith("zh-tw") || lower.startsWith("zh-hant")) {
    return "zh-TW";
  }
  if (lower.startsWith("zh")) {
    return "zh-CN";
  }
  if (lower.startsWith("ja")) {
    return "ja";
  }
  if (lower.startsWith("ko")) {
    return "ko";
  }
  if (lower.startsWith("es")) {
    return "es";
  }
  if (lower.startsWith("fr")) {
    return "fr";
  }
  if (lower.startsWith("de")) {
    return "de";
  }
  if (lower.startsWith("pt")) {
    return "pt-BR";
  }
  if (lower.startsWith("ru")) {
    return "ru";
  }
  return "en";
}
