import { html, nothing } from "lit";
import type { GatewayHelloOk } from "../gateway.ts";
import type { UiSettings } from "../storage.ts";
import { formatRelativeTimestamp, formatDurationHuman } from "../format.ts";
import { icons } from "../icons.ts";
import { uiText } from "../i18n.ts";
import { formatNextRun } from "../presenter.ts";
import type { Locale } from "./onboarding/i18n.ts";

export type OverviewProps = {
  locale: Locale;
  connected: boolean;
  hello: GatewayHelloOk | null;
  settings: UiSettings;
  password: string;
  lastError: string | null;
  presenceCount: number;
  sessionsCount: number | null;
  cronEnabled: boolean | null;
  cronNext: number | null;
  lastChannelsRefresh: number | null;
  onSettingsChange: (next: UiSettings) => void;
  onPasswordChange: (next: string) => void;
  onSessionKeyChange: (next: string) => void;
  onConnect: () => void;
  onRefresh: () => void;
  onNavigate?: (tab: string) => void;
};

export function renderOverview(props: OverviewProps) {
  const t = (english: string, chinese: string) => uiText(props.locale, english, chinese);
  const snapshot = props.hello?.snapshot as
    | { uptimeMs?: number; policy?: { tickIntervalMs?: number } }
    | undefined;
  const uptime = snapshot?.uptimeMs ? formatDurationHuman(snapshot.uptimeMs) : t("n/a", "");
  const tick = snapshot?.policy?.tickIntervalMs ? `${snapshot.policy.tickIntervalMs}ms` : t("n/a", "");
  const authHint = (() => {
    if (props.connected || !props.lastError) {
      return null;
    }
    const lower = props.lastError.toLowerCase();
    const authFailed = lower.includes("unauthorized") || lower.includes("connect failed");
    if (!authFailed) {
      return null;
    }
    const hasToken = Boolean(props.settings.token.trim());
    const hasPassword = Boolean(props.password.trim());
    if (!hasToken && !hasPassword) {
      return html`
        <div class="muted" style="margin-top: 8px">
          ${t("This gateway requires auth. Add a token or password, then click Connect.", " token ")}
          <div style="margin-top: 6px">
            <span class="mono">opensoul dashboard --no-open</span>  ${t("open the Control UI", "")}<br />
            <span class="mono">opensoul doctor --generate-gateway-token</span>  ${t("set token", " token")}
          </div>
          <div style="margin-top: 6px">
            <a
              class="session-link"
              href="https://docs.opensoul.ai/web/dashboard"
              target="_blank"
              rel="noreferrer"
              title=${t("Control UI auth docs (opens in new tab)", "")}
              >${t("Docs: Control UI auth", "")}</a
            >
          </div>
        </div>
      `;
    }
    return html`
      <div class="muted" style="margin-top: 8px">
        ${t("Auth failed. Update the token or password in Control UI settings, then click Connect.", " token ")}
        <div style="margin-top: 6px">
          <a
            class="session-link"
            href="https://docs.opensoul.ai/web/dashboard"
            target="_blank"
            rel="noreferrer"
            title=${t("Control UI auth docs (opens in new tab)", "")}
            >${t("Docs: Control UI auth", "")}</a
          >
        </div>
      </div>
    `;
  })();
  const insecureContextHint = (() => {
    if (props.connected || !props.lastError) {
      return null;
    }
    const isSecureContext = typeof window !== "undefined" ? window.isSecureContext : true;
    if (isSecureContext) {
      return null;
    }
    const lower = props.lastError.toLowerCase();
    if (!lower.includes("secure context") && !lower.includes("device identity required")) {
      return null;
    }
    return html`
      <div class="muted" style="margin-top: 8px">
        ${t(
          "This page is HTTP, so the browser blocks device identity. Use HTTPS (Tailscale Serve) or open",
          " HTTP HTTPSTailscale Serve",
        )}
        <span class="mono">http://127.0.0.1:18789</span>
        ${t("on the gateway host.", "")}
        <div style="margin-top: 6px">
          ${t("If you must stay on HTTP, set", " HTTP")}
          <span class="mono">gateway.controlUi.allowInsecureAuth: true</span> (token-only).
        </div>
        <div style="margin-top: 6px">
          <a
            class="session-link"
            href="https://docs.opensoul.ai/gateway/tailscale"
            target="_blank"
            rel="noreferrer"
            title=${t("Tailscale Serve docs (opens in new tab)", "Tailscale Serve ")}
            >${t("Docs: Tailscale Serve", "Tailscale Serve")}</a
          >
          <span class="muted">  </span>
          <a
            class="session-link"
            href="https://docs.opensoul.ai/web/control-ui#insecure-http"
            target="_blank"
            rel="noreferrer"
            title=${t("Insecure HTTP docs (opens in new tab)", " HTTP ")}
            >${t("Docs: Insecure HTTP", " HTTP")}</a
          >
        </div>
      </div>
    `;
  })();

  return html`
    <section class="grid grid-cols-2">
      <div class="card">
        <div class="card-title">${t("Gateway Access", "")}</div>
        <div class="card-sub">${t("Where the dashboard connects and how it authenticates.", "")}</div>
        <div class="form-grid" style="margin-top: 16px;">
          <label class="field">
            <span>${t("WebSocket URL", "WebSocket ")}</span>
            <input
              .value=${props.settings.gatewayUrl}
              @input=${(e: Event) => {
                const v = (e.target as HTMLInputElement).value;
                props.onSettingsChange({ ...props.settings, gatewayUrl: v });
              }}
              placeholder="ws://100.x.y.z:18789"
            />
          </label>
          <label class="field">
            <span>${t("Gateway Token", " Token")}</span>
            <input
              .value=${props.settings.token}
              @input=${(e: Event) => {
                const v = (e.target as HTMLInputElement).value;
                props.onSettingsChange({ ...props.settings, token: v });
              }}
              placeholder="OPENSOUL_GATEWAY_TOKEN"
            />
          </label>
          <label class="field">
            <span>${t("Password (not stored)", "")}</span>
            <input
              type="password"
              .value=${props.password}
              @input=${(e: Event) => {
                const v = (e.target as HTMLInputElement).value;
                props.onPasswordChange(v);
              }}
              placeholder=${t("system or shared password", "")}
            />
          </label>
          <label class="field">
            <span>${t("Default Session Key", " Key")}</span>
            <input
              .value=${props.settings.sessionKey}
              @input=${(e: Event) => {
                const v = (e.target as HTMLInputElement).value;
                props.onSessionKeyChange(v);
              }}
            />
          </label>
        </div>
        <div class="row" style="margin-top: 14px;">
          <button class="btn" @click=${() => props.onConnect()}>${t("Connect", "")}</button>
          <button class="btn" @click=${() => props.onRefresh()}>${t("Refresh", "")}</button>
          <span class="muted">${t("Click Connect to apply connection changes.", "")}</span>
        </div>
      </div>

      <div class="card">
        <div class="card-title">${t("Snapshot", "")}</div>
        <div class="card-sub">${t("Latest gateway handshake information.", "")}</div>
        <div class="stat-grid" style="margin-top: 16px;">
          <div class="stat">
            <div class="stat-label">${t("Status", "")}</div>
            <div class="stat-value ${props.connected ? "ok" : "warn"}">
              ${props.connected ? t("Connected", "") : t("Disconnected", "")}
            </div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Uptime", "")}</div>
            <div class="stat-value">${uptime}</div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Tick Interval", "")}</div>
            <div class="stat-value">${tick}</div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Last Channels Refresh", "")}</div>
            <div class="stat-value">
              ${props.lastChannelsRefresh ? formatRelativeTimestamp(props.lastChannelsRefresh) : t("n/a", "")}
            </div>
          </div>
        </div>
        ${
          props.lastError
            ? html`<div class="callout danger" style="margin-top: 14px;">
              <div>${props.lastError}</div>
              ${authHint ?? ""}
              ${insecureContextHint ?? ""}
            </div>`
            : html`
                <div class="callout" style="margin-top: 14px">
                  ${t("Use Channels to link WhatsApp, Telegram, Discord, Signal, or iMessage.", " WhatsAppTelegramDiscordSignal  iMessage")}
                </div>
              `
        }
      </div>
    </section>

    <section class="grid grid-cols-4" style="margin-top: 18px;">
      <div class="card stat-card">
        <div class="stat-label">${t("Status", "")}</div>
        <div class="stat-value ${props.connected ? "ok" : "warn"}">
          ${props.connected ? t("Online", "") : t("Offline", "")}
        </div>
        <div class="muted">${t("Gateway connection state.", "")}</div>
      </div>
      <div class="card stat-card">
        <div class="stat-label">${t("Instances", "")}</div>
        <div class="stat-value">${props.presenceCount}</div>
        <div class="muted">${t("Active beacons in the last 5 min.", " 5 ")}</div>
      </div>
      <div class="card stat-card">
        <div class="stat-label">${t("Sessions", "")}</div>
        <div class="stat-value">${props.sessionsCount ?? t("n/a", "")}</div>
        <div class="muted">${t("Tracked session keys.", " Key ")}</div>
      </div>
      <div class="card stat-card">
        <div class="stat-label">${t("Cron", "")}</div>
        <div class="stat-value">
          ${props.cronEnabled == null
            ? t("n/a", "")
            : props.cronEnabled
              ? t("Enabled", "")
              : t("Disabled", "")}
        </div>
        <div class="muted">${t("Next wake", "")} ${formatNextRun(props.cronNext)}</div>
      </div>
    </section>

    <section class="grid grid-cols-2" style="margin-top: 18px;">
      <!-- System Information -->
      <div class="card">
        <div class="card-title">${t("System Information", "")}</div>
        <div class="card-sub">${t("Runtime details from the gateway handshake.", "")}</div>
        <div class="stat-grid" style="margin-top: 16px;">
          <div class="stat">
            <div class="stat-label">${t("Protocol Version", "")}</div>
            <div class="stat-value" style="font-size:18px;">${props.hello?.protocol ?? t("n/a", "")}</div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Uptime", "")}</div>
            <div class="stat-value" style="font-size:18px;">${uptime}</div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Tick Interval", "")}</div>
            <div class="stat-value" style="font-size:18px;">${tick}</div>
          </div>
          <div class="stat">
            <div class="stat-label">${t("Last Channels Refresh", "")}</div>
            <div class="stat-value" style="font-size:18px;">
              ${props.lastChannelsRefresh ? formatRelativeTimestamp(props.lastChannelsRefresh) : t("n/a", "")}
            </div>
          </div>
          ${
            props.hello?.features?.methods
              ? html`<div class="stat" style="grid-column: 1 / -1;">
                <div class="stat-label">${t("Available Methods", "")}</div>
                <div class="muted" style="margin-top:4px;font-size:12px;word-break:break-word;">
                  ${props.hello.features.methods.join(", ")}
                </div>
              </div>`
              : nothing
          }
        </div>
      </div>

      <!-- Quick Actions -->
      <div class="card">
        <div class="card-title">${t("Quick Actions", "")}</div>
        <div class="card-sub">${t("Navigate to common tasks quickly.", "")}</div>
        <div class="overview-actions" style="margin-top: 16px; display: grid; gap: 10px;">
          <button class="btn" style="justify-content: flex-start; width: 100%;"
            @click=${() => props.onNavigate?.("chat")}>
            ${icons.messageSquare}
            <span>${t("Open Chat", "")}</span>
          </button>
          <button class="btn" style="justify-content: flex-start; width: 100%;"
            @click=${() => props.onNavigate?.("channels")}>
            ${icons.link}
            <span>${t("View Channels", "")}</span>
          </button>
          <button class="btn" style="justify-content: flex-start; width: 100%;"
            @click=${() => props.onNavigate?.("sessions")}>
            ${icons.fileText}
            <span>${t("Manage Sessions", "")}</span>
          </button>
          <button class="btn" style="justify-content: flex-start; width: 100%;"
            @click=${() => props.onNavigate?.("logs")}>
            ${icons.scrollText}
            <span>${t("View Logs", "")}</span>
          </button>
          <button class="btn" style="justify-content: flex-start; width: 100%;"
            @click=${() => props.onNavigate?.("config")}>
            ${icons.settings}
            <span>${t("Edit Config", "")}</span>
          </button>
        </div>
      </div>
    </section>

    <section class="card" style="margin-top: 18px;">
      <div class="card-title">${t("Getting Started", "")}</div>
      <div class="card-sub">${t("Quick guides to help you get the most out of OpenSoul.", " OpenSoul ")}</div>
      <div class="note-grid" style="margin-top: 14px;">
        <div>
          <div class="note-title">${t("1. Connect your gateway", "1. ")}</div>
          <div class="muted">
            ${t(
              "Enter the WebSocket URL and token above, then click Connect. For remote access, use Tailscale serve.",
              " WebSocket  token  Tailscale Serve",
            )}
          </div>
        </div>
        <div>
          <div class="note-title">${t("2. Link messaging channels", "2. ")}</div>
          <div class="muted">
            ${t(
              "Go to Channels to connect WhatsApp, Telegram, Discord, Signal, or iMessage.",
              " WhatsAppTelegramDiscordSignal  iMessage",
            )}
          </div>
        </div>
        <div>
          <div class="note-title">${t("3. Chat and automate", "3. ")}</div>
          <div class="muted">
            ${t(
              "Use the Chat view for real-time interaction, or set up Cron for scheduled automated runs.",
              "",
            )}
          </div>
        </div>
      </div>
    </section>
  `;
}
